import Vue from 'vue';
//
import VueRouter from 'vue-router'
Vue.use(VueRouter);
//1.
import Index from '@/views/index'
import Home from '@/views/home'
import Board from '@/views/board'
import Login from '@/views/login'
import NotFound from '@/views/404'
import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'
	import news from '@/views/modules/news/list'
	import discussstzc from '@/views/modules/discussstzc/list'
	import devicedetection from '@/views/modules/devicedetection/list'
	import yonghu from '@/views/modules/yonghu/list'
	import devicedetectionforecast from '@/views/modules/devicedetectionforecast/list'
	import stzc from '@/views/modules/stzc/list'
	import config from '@/views/modules/config/list'
	import newstype from '@/views/modules/newstype/list'


//2.   
export const routes = [{
	path: '/',
	name: '',
	component: Index,
	children: [{
		// main
		path: '/',
		name: '',
		component: Home,
		meta: {icon:'', title:'center', affix: true}
	}, {
		path: '/updatePassword',
		name: '',
		component: UpdatePassword,
		meta: {icon:'', title:'updatePassword'}
	}, {
		path: '/pay',
		name: '',
		component: pay,
		meta: {icon:'', title:'pay'}
	}, {
		path: '/center',
		name: '',
		component: center,
		meta: {icon:'', title:'center'}
	}
	,{
		path: '/news',
		name: '',
		component: news
	}
	,{
		path: '/discussstzc',
		name: '',
		component: discussstzc
	}
	,{
		path: '/devicedetection',
		name: '',
		component: devicedetection
	}
	,{
		path: '/yonghu',
		name: '',
		component: yonghu
	}
	,{
		path: '/devicedetectionforecast',
		name: '',
		component: devicedetectionforecast
	}
	,{
		path: '/stzc',
		name: '',
		component: stzc
	}
	,{
		path: '/config',
		name: '',
		component: config
	}
	,{
		path: '/newstype',
		name: '',
		component: newstype
	}
	]
	},
	{
		path: '/login',
		name: 'login',
		component: Login,
		meta: {icon:'', title:'login'}
	},
	{
		path: '/board',
		name: 'board',
		component: Board,
		meta: {icon:'', title:'board'}
	},
	{
		path: '/register',
		name: 'register',
		component: register,
		meta: {icon:'', title:'register'}
	},
	{
		path: '*',
		component: NotFound
	}
]
//3.VueRouter  
const router = new VueRouter({
	mode: 'hash',
	/*hashhistory*/
	routes //  routes: routes
})
const originalPush = VueRouter.prototype.push
//push
VueRouter.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err)
}
export default router;
